/* Estilos generales del modal */
.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8); 
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Estilos del contenido del modal */
.modal-content {
  background-color: #ffffff; 
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(7, 7, 7, 0.975); 
  position: relative;
  width: 90%; 
  max-width: 400px; 
  text-align: center;
}

/* Estilos del botón de cierre del modal */
.close-button {
  position: absolute;
  top: 12px;
  right: 12px;
  font-size: 18px;
  color: #333;
  cursor: pointer;
  background-color: white; 
  border: none; 
  padding: 5px; 
}

.close-button:hover {
  background-color: transparent; 
}

/* Resto de los estilos */

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.header img {
  margin-right: 10px;
}

.form-group {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
}

.form-group img {
  margin-right: 10px;
  width: 30px; 
  height: 30px; 
}

.form-group input {
  width: calc(100% - 40px); 
  padding: 10px;
  border: 1px solid #FFA500; 
  border-radius: 5px;
  font-size: 14px;
}

.center-button {
  text-align: center;
  margin-top: 20px;
}

.login-button {
  padding: 10px 20px; 
  background-color: #FFA500; 
  color: white; 
  border: none;
  border-radius: 3px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.login-button:hover {
  background-color: #FFA500; 
}

.error-message {
  color: red; 
  margin-top: 10px;
  font-size: 14px;
}

/* Estilos responsivos */
@media (max-width: 768px) {
  .modal-content {
    width: 90%; 
    max-width: 90%; 
  }
}
