/* Estilos para los contenedores de productos */
.productos-container {
  margin: 20px;
}

/* Estilos para la cuadrícula de productos */
.productos-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
}

/* Estilos para las tarjetas de producto */
.producto-card {
  background-color: #FFFFFF;
  border: 1px solid #8f8f8f;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

/* Estilos para la imagen del producto */
.producto-imagen {
  width: 120px;
  height: auto;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

/* Estilos para la información del producto */
.producto-info {
  text-align: center;
  margin-top: 10px;
}

/* Estilos para el nombre del producto */
.producto-info h3 {
  margin: 5px 0;
  font-size: 16px;
}

/* Estilos para la descripción y el precio */
.producto-info p {
  margin: 5px 0;
  font-size: 14px;
}

/* Estilos para los botones de administrador */
.admin-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Estilos para los botones de administrador */
.admin-buttons button {
  background-color: #f44336;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 15px;
  display: flex; /* Añadido */
  justify-content: center; /* Añadido */
  align-items: center; /* Añadido */
}

.admin-buttons button:hover {
  background-color: #d32f2f;
}

.admin-buttons button:last-child {
  margin-left: 4px;
}

/* Estilos para el botón de edición */
.editar-button {
  background-color: transparent;
  border: 2px solid #f44336;
  border-radius: 50%;
  cursor: pointer;
  color: #f44336;
  font-size: 20px; /* Ajustar el tamaño del ícono */
  width: 40px; /* Ajustar el ancho */
  height: 40px; /* Ajustar la altura */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0; /* Eliminar el padding */
}

/* Estilos para el ícono de edición */
.editar-button .fa-pencil-alt {
  font-size: 15px; /* Ajustar el tamaño del ícono */
}


/* Media queries para hacer los estilos responsivos */
@media (max-width: 768px) {
  .productos-grid {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 15px;
  }

  .producto-card {
    padding: 15px;
  }

  .producto-imagen {
    width: 100px;
  }

  .producto-info h3 {
    font-size: 14px;
  }

  .producto-info p {
    font-size: 12px;
  }

  .admin-buttons button {
    padding: 4px 8px;
    font-size: 12px;
  }

  .editar-button {
    font-size: 30px;
    padding: 10px;
    width: 20px;
    height: 20px;
  }
}

/* Estilos para la barra de búsqueda */
.search-bar-container {
  max-width: 300px; /* Ajusta este valor según tus necesidades */
  margin: 30 auto; /* Centrar horizontalmente */
}

.search-bar {
  display: flex;
  align-items: center; /* Centrar verticalmente */
}

.search-bar input {
  width: 120px; /* Reducir el ancho del input */
  padding: 8px 12px; /* Padding interno */
  font-size: 14px; /* Tamaño de la fuente */
  border: 1px solid #ccc; /* Borde delgado gris */
  border-radius: 4px; /* Borde redondeado */
  box-sizing: border-box; /* Incluir el borde en el tamaño total */
}

.search-bar input:focus {
  outline: none; /* Eliminar el contorno al enfocar */
  border-color: #FFA500; /* Cambiar el color del borde al enfocar */
}

/* Estilos para el icono de búsqueda */
.search-icon {
  margin-left: 8px; /* Espacio a la izquierda del icono */
  color: #555; /* Color del icono */
  cursor: pointer; /* Cursor al pasar sobre el icono */
}
