/* Estilos para el contenedor del botón de edición */
.editar-promocion-container {
  display: inline-block;
}

/* Estilos para el botón de edición */
.editar-promocion-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

/* Estilos para el ícono de lápiz */
.editar-promocion-icon {
  color: #333;
  font-size: 20px;
}

/* Estilos para el botón de cancelar */
.editar-promocion-button.cancel {
  background-color: #dc3545;
  color: white;
}

/* Estilos para el modal de edición */
.custom-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: auto;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(7, 7, 7, 0.975);
  z-index: 1000;
}

/* Estilos para el botón de cerrar del modal */
.custom-modal .close {
  position: absolute;
  top: 12px;
  right: -4px;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 18px;
  color: #333;
}

/* Estilos para el título del modal */
.custom-modal h2 {
  text-align: center;
  margin-bottom: 20px;
}

/* Estilos para los campos del formulario dentro del modal */
.custom-modal input[type="text"],
.custom-modal input[type="number"],
.custom-modal textarea,
.custom-modal select {
  width: calc(100% - 10px);
  padding: 8px;
  margin-bottom: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

/* Estilos para los botones dentro del modal */
.custom-modal .botones-container {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.custom-modal .btn-action {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: calc(50% - 10px);
  background-color: #28a745;
  color: white;
}

.custom-modal .btn-action:hover {
  background-color: #218838;
}

.custom-modal .btn-actionm {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: calc(50% - 10px);
  background-color: red;
  color: white;
}

/* Estilos para el mensaje de error */
.custom-modal .error-message {
  color: red;
  margin-top: 10px;
}

@media screen and (max-width: 768px) {
  /* Estilos adicionales para dispositivos móviles */
  .custom-modal {
    width: 90%;
    max-width: 400px;
    padding: 15px;
  }
}
