/* Estilos para la modal de eliminar promoción */
.eliminar-promocion {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(255, 255, 255, 0.8); /* Fondo semi-transparente */
    padding: 20px;
    border-radius: 6px;
    box-shadow: 0 0 10px rgba(7, 7, 7, 0.975);
    width: 300px;
    height: auto;
}

/* Estilos para el título del modal de eliminar promoción */
.eliminar-promocion-title {
    margin-top: 0;
    font-size: 1.2rem;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
    display: flex; /* Utilizar flexbox */
    align-items: center; /* Centrar verticalmente */
    justify-content: center; /* Centrar horizontalmente */
    height: 60px; /* Altura fija para el contenedor del título */
}

/* Estilos para el texto dentro del modal de eliminar promoción */
.eliminar-promocion-text {
    font-size: 1rem;
    margin-bottom: 10px;
    text-align: center;
}

/* Estilos para el contenedor de los botones dentro del modal de eliminar promoción */
.eliminar-promocion-button-container {
    text-align: center; 
}

/* Estilos para los botones dentro del modal de eliminar promoción */
.eliminar-promocion-button {
    display: inline-block; 
    margin-top: 40px;
    padding: 10px 1px; 
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: none; 
}

/* Estilos para el botón "Sí" del modal de eliminar promoción */
.eliminar-promocion-button-yes {
    background-color: #28a745 !important;
    color: white !important;
    margin-right: 10px; 
}

/* Estilos para el botón "Cancelar" del modal de eliminar promoción */
.eliminar-promocion-button-no {
    background-color: #dc3545 !important;
    color: white;
}

.eliminar-promocion-button:hover {
    opacity: 1; 
}
