/* Estilos generales del modal */
.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
}

/* Estilos del fondo detrás del modal */
.modal-background-crear-usuario {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color:  rgba(255, 255, 255, 0.5);
  z-index: 999; 
}

/* Estilos del contenido del modal */
.modal-content {
  background-color: #fefefe;
  margin: 10% auto;
  padding: 20px;
  border-radius: 8px;
  max-width: 80%; /* Ajustar el ancho máximo */
}

/* Estilos del botón de cierre del modal */
.close-button {
  color: #aaa;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
  position: absolute; 
  top: 20px;
  right: 20px;
}

.close-button:hover {
  color: black;
}

/* Estilos del mensaje de error */
.error-message {
  color: red;
  margin-bottom: 10px;
}

/* Estilos de etiquetas y campos de entrada */
label {
  display: block;
  margin-bottom: 5px;
}

input[type="text"],
input[type="password"],
button {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

/* Estilos del botón de inserción */
.insertar-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  padding: 10px 0px; 
  border-radius: 5px;
  margin-right: 10px; 
}

.insertar-button:hover {
  background-color: #45a049;
}

/* Estilos del botón de ver/ocultar */
.ver-button {
  background-color: #FFA500;
  color: white;
  border: none;
  cursor: pointer;
  padding: 10px 20px; 
  border-radius: 5px;
  margin-left: 10px; 
}

.ver-button:hover {
  background-color: #FFA500;
}

/* Estilos de la lista de usuarios */
ul {
  list-style-type: none;
  padding: 0;
}

ul li {
  margin-bottom: 5px;
}

/* Estilos de la tabla de usuarios */
.table-container {
  margin-top: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table th {
  border: 1px solid #FFA500;
  text-align: left;
  padding: 8px;
  background-color: #FFA500;
  color: white; 
}

table td {
  border: 1px solid #FFA500;
  text-align: left;
  padding: 8px;
  background-color: white;
}

table td.username,
table td.password {
  color: black; 
}

table tr:nth-child(even) {
  background-color: white;
}

table tr:hover {
  background-color: white;
}

/* Estilos personalizados para los botones de editar y eliminar */
.edit-button,
.delete-button {
  background-color: transparent !important; /* Hacer transparente el fondo */
  border: none;
  cursor: pointer; 
  outline: none; 
  box-shadow: none; 
}

/* Media queries para estilos responsivos */
@media screen and (max-width: 768px) {
  .modal-content {
    max-width: 90%; /* Reducir el ancho máximo en pantallas pequeñas */
  }
}
