/* Estilos para el fondo semitransparente */
.modal-background-promocion {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8); /* Fondo semi-transparente */
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Estilos para el contenedor del modal */
.modal-promocion {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 280px; /* Reducir el ancho del contenedor */
  max-width: 80%; /* Ancho máximo del contenedor */
  height: auto;
  background-color: white; /* Fondo blanco */
  padding: 16px; /* Reducir el relleno */
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(22, 22, 22, 0.2);
  z-index: 10000;
}

/* Estilos para la x de cierre */
.modal-promocion .close-promocion {
  position: absolute;
  top: 8px; /* Ajustar la posición de la x */
  right: 8px; /* Ajustar la posición de la x */
  cursor: pointer;
  background: none;
  border: none;
  font-size: 20px; /* Reducir el tamaño de la x */
  color: #333;
}

/* Estilos para el título del modal */
.modal-promocion h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 22px; /* Agrandar el tamaño del título */
}

/* Estilos para los campos del formulario dentro del modal */
.modal-promocion input[type="text"],
.modal-promocion input[type="number"],
.modal-promocion textarea,
.modal-promocion select {
  width: calc(100% - 16px); /* Ajustar el ancho de los campos */
  padding: 8px; /* Reducir el tamaño del relleno */
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 14px; /* Reducir el tamaño de la fuente */
}

/* Estilos para los botones dentro del modal */
.modal-promocion .botones-container-promocion {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.modal-promocion .botones-container-promocion button {
  padding: 8px 16px; /* Reducir el tamaño del relleno de los botones */
  border-radius: 4px;
  cursor: pointer;
  width: calc(50% - 8px); /* Ajustar el ancho de los botones */
  background-color: #28a745;
  color: white;
  font-size: 14px; /* Reducir el tamaño de la fuente de los botones */
}

.modal-promocion .botones-container-promocion button:hover {
  background-color: #218838;
}

/* Estilos para el mensaje de error */
.modal-promocion .error-message-promocion {
  color: red;
  margin-top: 10px;
  font-size: 12px; /* Reducir el tamaño de la fuente del mensaje de error */
}

/* Media queries para estilos responsivos */
@media screen and (max-width: 768px) {
  .modal-promocion {
    width: 90%;
  }
}
