body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f8f8;
}

header {
  background-color: #333;
  color: #fff;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-icon {
  cursor: pointer;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  height: 25px;
}

.bar {
  background-color: #fff;
  height: 3px;
  width: 25px;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav ul li {
  margin-right: 20px;
}

section {
  padding: 20px;
  background-color: #fff;
  margin: 10px 0;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.modal {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #88888896;
  width: 80%;
  max-width: 400px;
  border-radius: 8px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
}

button {
  background-color: #333333;
  color: #fff;
  padding: 8px;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  width: 20%;
}

button:hover {
  background-color: #555;
}

@media screen and (max-width: 768px) {
  .menu-icon {
    display: flex;
  }

  nav ul {
    display: none;
  }

  section {
    padding: 10px;
  }

  .producto-item {
    width: calc(100% - 30px);
  }
}
