/* Estilos para el encabezado */
header {
  background: linear-gradient(135deg, #FFFFFF, #FFA500) !important; 
  color: #fff;
  padding: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; 
}


/* Estilos para el logo */
.logo {
  max-width: 300px; /* Ancho máximo del logo */
  border-radius: 100%; /* Forma circular */
}


/* Estilos para el título */
h1 {
  margin: 0;
  flex-grow: 1; 
  text-align: center; 
}

/* Estilos para la navegación */
nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between; /* Distribuye los elementos horizontalmente */
  align-items: center; /* Alinea los elementos verticalmente al centro */
  height: 60px; /* Alto fijo para la barra de navegación */
}

nav ul li {
  margin-right: 16px;
}

nav ul li:last-child {
  margin-right: 0; /* Elimina el margen derecho del último elemento */
}

nav ul li a,
nav ul li button {
  color: #fff;
  text-decoration: none;
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 15px;
}

/* Ajuste del margen entre elementos de la barra de navegación */
nav ul li {
  margin-right: 20px;
}

/* Estilos para el botón de inicio de sesión y cierre de sesión */
.btn-iniciar-sesion,
.btn-cerrar-sesion {
  background-color: transparent; /* Fondo transparente por defecto */
  border: none; /* Eliminar el borde */
  color: #fff; /* Color de texto blanco */
  cursor: pointer;
  transition: background-color 0.3s ease; /* Transición suave para el color de fondo */
  font-size: 14px; /* Tamaño de letra ajustado */
  outline: none; /* Eliminar el contorno al hacer clic */
  padding: 0; /* Eliminar el padding */
  margin: 0; /* Eliminar el margen */
}

/* Cambiar el color de fondo al naranja cuando se pasa el cursor */
.btn-iniciar-sesion:hover,
.btn-cerrar-sesion:hover {
  background-color: #FFA500; /* Cambiar a color naranja en estado hover */
}




/* Estilos para el botón flotante */
.floating-button-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 999;
}

.floating-button {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #FFA500; /* Cambiado a color naranja */
  color: white;
  font-size: 24px;
  border: none;
  cursor: pointer;
}

/* Eliminar cambio de color al pasar el cursor */
.floating-button:hover {
  background-color: #FFA500;
}

/* Estilos para el menú desplegable */
.menu {
  position: fixed;
  bottom: 90px;
  right: 30px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 0px 10px #FFA500;
  padding: 10px;
  z-index: 999;
}

.menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu ul li {
  margin-bottom: 5px;
}

.menu ul li a {
  text-decoration: none;
  color: black;
  font-size: 16px;
}

.menu ul li a:hover {
  color: blue;
}

/* Estilos para los botones del menú desplegable */
.menu ul li button {
  background-color: transparent;
  border: 2px solid #FFA500;
  color: #FFA500;
  cursor: pointer;
  font-size: 16px;
  width: 180px;
  height: 40px;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
  margin-right: 10px;
}

.menu ul li button:hover {
  background-color: #FFA500;
  color: white;
}

/* Estilos para el ajuste de margen entre elementos del menú */
.menu ul li {
  margin-bottom: 10px;
}

/* Estilos para los botones "Crear Promoción" y "Crear Usuario" en la sección de inicio */
.botones-inicio {
  display: flex;
  flex-wrap: wrap; 
  justify-content: center;
}

.botones-inicio button {
  width: 15%;
  height: 30px;
  font-size: 14px;
  margin-right: 1px;
  margin-bottom: 5px;
  background-color: #FFA500; /* Color de fondo naranja */
  color: white; /* Color de texto blanco */
  border: none; /* Sin borde */
  cursor: pointer; /* Cursor apuntador */
  transition: background-color 0.3s ease; /* Transición suave para el color de fondo */
}

/*Cambiar el color de fondo al naranja cuando se pasa el cursor */
.botones-inicio button:hover {
  background-color: #FF7F00; /* Cambiar a un tono más oscuro de naranja en estado hover */
}

/* Estilos para la navegación en pantallas pequeñas */
@media screen and (max-width: 768px) {
  .menu-icon {
    display: flex;
    position: absolute;
    top: 15px;
    right: 20px;
    flex-direction: column;
    justify-content: space-between;
    height: 20px;
    z-index: 1000;
  }

  .menu-icon .bar {
    background-color: #fff;
  }

  .nav-links {
    display: none;
  }

  .nav-links.open {
    display: flex;
  }
}



.contact-section {
  background-color: #f9f9f9;
  padding: 50px 0;
  text-align: center;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

.section-title {
  color: #333;
  font-size: 28px; /* Reduzco el tamaño de la fuente del título */
  margin-bottom: 30px;
}

.contact-info {
  margin-bottom: 20px;
}

.info-text {
  font-size: 16px; /* Reduzco el tamaño de la fuente del texto */
  margin-bottom: 10px;
}

.contact-detail {
  color: #FFA500;
}

.icon {
  margin-right: 10px;
}
